  display: none;
}

.contenido-centrado {
  max-width: 800px;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
}
.footer__box {
  padding-top: 2.5rem;
}
.footer .copyrigth {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .footer__box-nav {
    display: flex;
  }
}
.footer__box-nav a {
  color: #FFFFFF;
  font-size: 2.2rem;
  display: block;
}
@media (min-width: 768px) {
  .footer__box-nav a {
    margin-right: 2rem;
  }
  .footer__box-nav a:last-of-type {
    margin-right: 0;
  }
}

.no-webp .home {
  background-image: url("../img/header.jpg");
}

.webp .home {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.home {
    min-height: 70rem;
  }
}
.header.home .header__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header.home .header__box {
    min-height: 70rem;
  }
}
.header.home .header__box h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 300;
  font-size: 2rem;
  max-width: 30rem;
}
@media (min-width: 768px) {
  .header.home .header__box h1 {
    font-size: 4rem;
    max-width: 60rem;
  }
}

.header__box--bar {
  display: flex;
  padding-top: 3rem;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__box--bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header__box--bar .bar__logo {
  min-width: 25rem;
  width: 40%;
  color: #FFFFFF;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .header__box--bar .bar__logo {
    width: auto;
  }
}
@media (min-width: 768px) {
  .header__box--bar .bar__nav {
    display: flex;
  }
}

.bar__logo h1 {
  font-weight: 300;
}
.bar__logo h1 span {
  font-weight: 700;
}

.bar__nav a {
  color: #FFFFFF;
  font-size: 2.2rem;
  display: block;
  transition: color 0.3s;
}
@media (min-width: 768px) {
  .bar__nav a {
    margin-right: 2rem;
  }
  .bar__nav a:last-of-type {
    margin-right: 0;
  }
}
.bar__nav a:hover {
  color: #71B100;
}
.bar__nav-responsive {
  background-color: #333333;
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10rem;
}
.bar__nav-responsive a {
  font-size: 4rem;
  margin: 0;
}

.section__anuncios--title {
  margin-bottom: 2rem;
}
.section__anuncios--content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .section__anuncios--content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  display: flex;
  flex-direction: column;
  background-color: #e1e1e1;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  overflow: hidden;
}
.anuncio__img {
  box-sizing: border-box;
  overflow: hidden;
}
.anuncio__img:hover img {
  transition: transform 0.3s ease;
  transform: scale(1.1);
}
.anuncio__content {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.anuncio__content {
  display: flex;
  flex-direction: column;
  gap: 1.1rem;
}
.anuncio__content--precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}
.anuncio__content--icons {
  display: flex;
  padding: 0;
  margin-bottom: 3rem;
}
.anuncio__content--icons li {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.anuncio__content--icons li img {
  flex-basis: 3rem;
}
.anuncio__content--icons li p {
  font-weight: 700;
}
.anuncio__content .button {
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  text-align: center;
}
.anuncio__content .button:hover {
  background-color: #71B100;
}

.view-all {
  display: flex;
  margin-bottom: 2rem;
  justify-content: flex-end;
}
.view-all .button {
  background-color: #71B100;
  color: #FFFFFF;
}
.view-all .button:hover {
  background-color: #E08709;
}

.contact {
  background-color: #333333;
  background-image: url("../img/encuentra.jpg");
  background-position: center;
  background-size: cover;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 0;
}
.contact h2, .contact p {
  max-width: 90%;
}
.contact h2 {
  font-weight: 700;
}
.contact p {
  color: #FFFFFF;
  text-align: center;
  font-weight: 300;
}
.contact .button {
  background-color: #E08709;
  color: #FFFFFF;
  margin-top: 2rem;
}
.contact .button:hover {
  background-color: #71B100;
}

.about__icons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .about__icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icon {
  text-align: center;
  border-bottom: 0.1rem solid #333333;
  margin-top: 4rem;
}
.icon:first-of-type {
  margin-top: 0;
}
@media (min-width: 768px) {
  .icon {
    margin: 0;
    border-bottom: none;
  }
}
.icon img {
  height: 10rem;
  width: auto;
  margin: 0 auto;
}
.icon h3 {
  text-transform: uppercase;
}

.section__blog {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .section__blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.section__blog--content {
  display: flex;
  flex-direction: column;
}
.section__blog--content .content__article {
  margin-bottom: 2rem;
}
.section__blog--content .content__article:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .section__blog--content .content__article {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    gap: 2rem;
    box-sizing: border-box;
  }
}

.content__article--img {
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  align-items: center;
}
.content__article--entrada {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.content__article--entrada h4, .content__article--entrada p {
  text-align: left;
}
.content__article--entrada h4 {
  line-height: 1.4;
  text-align: left;
  color: #000000;
  font-weight: 700;
}
.content__article--entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.content__article--entrada p {
  font-weight: 400;
}
.content__article--entrada span {
  color: #E08709;
}
.content__article--entrada .view-entrada {
  display: flex;
  justify-content: flex-end;
}
.content__article--entrada .view-entrada .button {
  background-color: #E08709;
  color: #FFFFFF;
  display: inline;
  width: fit-content;
}
.content__article--entrada .view-entrada .button:hover {
  background-color: #71B100;
}